import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

import { RootState } from "../store";
import Constants from "expo-constants";
import jwtDecode from "jwt-decode";

export interface User {
  id: string;
  username: string;
  email: string;
  iss: string;
  role: string[];
}

export interface LoginResponse {
  access_token: string;
  refresh_token: string;
  user_id: string;
}

export interface LoginRequest {
  username: string;
  password: string;
}

export const api = createApi({
  reducerPath: "api",
  baseQuery: fetchBaseQuery({
    baseUrl: Constants.manifest?.extra?.baseUrl,
    headers: {
      "content-type": "application/json",
    },
    prepareHeaders: (headers, { getState }) => {
      // By default, if we have a token in the store, let's use that for authenticated requests
      const token = (getState() as RootState).auth.access_token;
      if (token) {
        headers.set("authorization", `Bearer ${token}`);
      }
      return headers;
    },
  }),
  endpoints: (builder) => ({
    login: builder.mutation<LoginResponse, LoginRequest>({
      query: (credentials) => ({
        url: "api/v1/login",
        method: "POST",
        body: credentials,
      }),
      transformResponse: ({ access_token, refresh_token }) => ({
        user: jwtDecode(access_token),
        access_token,
        refresh_token,
      }),
    }),
    calculate: builder.mutation<any, any>({
      query: ({ operand1, operand2, client_id }) => ({
        url: "api/v1/calculators/add",
        method: "POST",
        body: {
          number1: operand1,
          number2: operand2,
          client_id,
        },
      }),
    }),
  }),
});

export const { useLoginMutation, useCalculateMutation } = api;
