import { View, Text, StyleSheet } from "react-native";
import { useCalculateMutation } from "../../app/services/auth";
import { useAuth } from "../../hooks/useAuth";
import { Button } from "../../components/Button";

export const Home = () => {
  const auth = useAuth();
  const [calculate, { isLoading }] = useCalculateMutation();

  const handleCalculate = async () => {
    const result = await calculate({
      operand1: 1,
      operand2: 2,
      client_id: auth.user?.id,
    });
    console.log(result);
  };

  return (
    <View style={style.container}>
      <Text>Username: {auth.user?.username}</Text>
      <Button text="Calculate" onClick={handleCalculate} disabled={isLoading} />
      {isLoading && <Text>Calculating...</Text>}
    </View>
  );
};

const style = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
    alignItems: "center",
    padding: 24,
  },
});
